<template> 
  <div class="selectcourse">
          
    <el-form :inline="true" :model="form" class="form-inline-query">
      <el-form-item label="课程编号" >
        <el-input v-model="form.num" placeholder="请输入课程编号" />
      </el-form-item>
      <el-form-item label="班级">
        <el-input v-model="form.class" placeholder="请输入班级"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button class="commButton" size="mini" type="primary" @click="doQuery()">查询</el-button>
      </el-form-item>
    </el-form> 
<el-scrollbar height="800px">
    <el-table  class="table-content"
        :data="dataList"
        border
        style="width: 100%;"
    >
      <el-table-column
          label="序号"
          fixed="left"
          width="50"          
          align="center"
          color="black"
          >
          <template v-slot="scope">
              {{ scope.$index+1 }}
          </template>
      </el-table-column>

        <el-table-column
        label="课程编号"
        align="center"
        color="black"
        sortable
        prop="courseNum" />

        <el-table-column
        label="课程名称"
        align="center"
        color="black"
        prop="courseName" />

        <el-table-column
        label="课程教师"
        align="center"
        color="black"
        sortable
        prop="courseteacher" />

        <el-table-column
        label="学生班级"
        align="center"
        color="black"
        sortable
        prop="stuclass" />

        <el-table-column
        label="学生学号"
        align="center"
        color="black"
        sortable
        prop="userNum" />

        <el-table-column
        label="学生姓名"
        align="center"
        color="black"
        sortable
        prop="userName" />

        <el-table-column
        label="成绩"
        align="center"
        color="black"
        sortable
        prop="courseselectmark" />
      </el-table>
      </el-scrollbar>
    </div>
</template>

<script>
import {getCoursemarkList} from '@/service/genServ.js'
export default {
  name: 'selectcourse',
  data() {
    return {
        sid:localStorage.getItem('sid'),
        usernum:localStorage.getItem('usernum'),
      form: {
        num:'',
        class:'',
      },
      dataList:[]
    }
  },
  created() {
      this.sid=localStorage.getItem('sid'),
      this.usernum=localStorage.getItem('usernum'),
      this.doQuery();
  },
  methods: {
    doQuery() {
       getCoursemarkList({'num':this.form.num, 'class':this.form.class}).then(res => {
        this.dataList = res.data;
      })
    },


  }
}
</script>

<style scoped>
.selectcourse{
  width: 1650px;
  position:absolute;
  top:100px;
  left:225px;
}
</style>
